import { useCallback } from "react"
import { DROP_ZONE_AREA_CLASS_NAME, DropZone } from "@rp/react/web/common/base-components"
import { useSnackbar } from "@rp/react/web/common/providers"
import { WatchlistsUploadOverlay } from "./WatchlistsUploadOverlay"

type Props = {
  children: React.ReactNode
  disabled?: boolean
  onDropFiles: (files: Array<File>) => void
  centerContent?: boolean
  onFilesRejected?: (props: { error: Error }) => void
}

export const WatchlistsUploadDropZone = ({
  children,
  disabled,
  onDropFiles,
  onFilesRejected,
  centerContent,
}: Props) => {
  const { enqueueError } = useSnackbar()

  const onDropRejected = useCallback(() => {
    const message = `The file format is not supported.`
    enqueueError(`The file format is not supported.`, {
      autoHideDuration: 5000,
    })
    onFilesRejected?.({
      error: new Error(message),
    })
  }, [enqueueError, onFilesRejected])

  if (disabled) return children

  return (
    <DropZone
      onDrop={onDropFiles}
      onDropRejected={onDropRejected}
      disabled={disabled}
      Overlay={<WatchlistsUploadOverlay centerContent={centerContent} />}
      accept=".csv"
      sx={{
        [`& .${DROP_ZONE_AREA_CLASS_NAME}`]: {
          height: "100%",
          position: "relative",
        },
      }}
    >
      {children}
    </DropZone>
  )
}
